* {
    margin: 0;
    padding: 0;
}

body {
    background-color: black;
    color: white;
}

.container {
    margin: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

#submit {
    background: white;
    border: none;
    outline: white;
    font-size: 17px;
    outline-style: auto;
    padding: 10px;
}

#height, #weight {
    margin-left: 1rem;
    border: 1px solid #848484;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 30px;
    outline: 0;
    height: 35px;
    width: 300px;
    padding-left: 10px;
    padding-right: 10px;
}

.height {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.weight {
    margin-bottom: 2rem;
}

label {
    margin-bottom: 1rem;
    display: block;
    font-size: 22px;
    text-align: center;
}

.submit, p {
    text-align: center;
}